.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.lfs-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 50px;
}
.lfs-center {
  max-width: 90%;
  /*float: right;*/
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  border: solid;
}

.lfs-look {
  border: solid;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
